// setactiveInstanceData(JSON.parse(String(localStorage.getItem('ActiveInstance'))))
export const instanceStore = new Store<instanceItem[] | false>(false)

export const instanceStore1 = new Store<instanceItem1 | false>(
  typeof localStorage === 'undefined'
    ? null
    : localStorage.getItem('ActiveInstance')
    ? JSON.parse(String(localStorage.getItem('ActiveInstance')))
    : false
)

export const instancestore = () => {
  const [instance, setinstance] = useStore(instanceStore)

  const [activeInstanceData, setactiveInstanceData] = useStore(instanceStore1)

  return {
    instance,
    setinstance,
    activeInstanceData,
    setactiveInstanceData,

    async loadinstance() {
      await getinstance(' ')
        .then(data => {
          setinstance(data.data.results)
          console.log('Instances Data: ', data.data.results)
        })
        .catch(err => {
          logerror(err, err.response)
        })
    },

    async loadinstancewithcallback(callback: any) {
      await getinstance(' ')
        .then(data => {
          console.log('Instances Data: ', data.data.results)
          callback(data.data.results)
        })
        .catch(err => {
          logerror(err, err.response)
        })
    },

    async loadactiveInstancebyID(id: any) {
      await getinstance(id)
        .then(data => {
          setactiveInstanceData(data.data.results[0])
        })
        .catch(err => {
          logerror(err, err.response)
        })
    },

    async postinstance(data: any) {
      await postinstance(data)
        .then(res => {
          console.log(res)
          setinstance((oldArray: any) => [...oldArray, res.data])
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },

    async editinstance(id: any, data: any) {
      await editinstance(id, data)
        .then(res => {
          console.log(res)
          setinstance(oldarr => {
            return (
              oldarr &&
              oldarr.map((obj: any) => {
                const getobj = (o: any) => {
                  let x = o
                  x.i_icon = data.i_icon
                  x.mascot = data.mascot
                  x.i_title = data.i_title
                  x.i_stitle = data.i_stitle
                  x.i_body = data.i_body
                  x.i_bodytype = data.i_bodytype
                  x.i_bodyoptions = data.i_bodyoptions
                  x.locations = data.locations
                  x.i_media = data.i_media
                  x.i_misc = data.i_misc
                  x.color_field = data.color_field
                  return x
                }
                return String(obj.id) === String(id) ? getobj(obj) : obj
              })
            )
          })
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },

    async deleteinstance(id: any) {
      await deleteinstance(id)
        .then(res => {
          console.log(res)
          setinstance(instance && instance.filter(ele => ele.id !== id))
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    }
  }
}
