import { Store, useStore } from './Store'
import getleveldetails from '../utils/api/getleveldetails'
import { logerror } from 'utils/actions'
export class leveldetailsItem {
  id: String
  l_level: String
  l_icon: String
  l_qrcode: String
  l_title: String
  l_stitle: String
  l_body: String
  l_bodytype: String
  l_bodyoptions: String
  locations: boolean
  l_media: String
  l_misc: String
  created_at: String
  updated_at: String
  instance_id: String
  SubLevelData: String
  GameDataLog: any
  unlock_after: String
  completion_image: String
  clue_image: String
  l_qr_id: String
  scan_complete: String
  progress: String
  is_locked: String
  TextData: any

  constructor(o: {
    id: String
    l_level: String
    l_icon: String
    l_qrcode: String
    l_title: String
    l_stitle: String
    l_body: String
    l_bodytype: String
    l_bodyoptions: String
    locations: boolean
    l_media: String
    l_misc: String
    created_at: String
    updated_at: String
    instance_id: String
    SubLevelData: any
    GameDataLog: any
    unlock_after: String
    completion_image: String
    clue_image: String
    l_qr_id: String
    scan_complete: String
    progress: String
    is_locked: String
    TextData: any
  }) {
    this.id = o.id
    this.l_level = o.l_level
    this.l_icon = o.l_icon
    this.l_qrcode = o.l_qrcode
    this.l_title = o.l_title
    this.l_stitle = o.l_stitle
    this.l_body = o.l_body
    this.l_bodytype = o.l_bodytype
    this.l_bodyoptions = o.l_bodyoptions
    this.locations = o.locations
    this.l_media = o.l_media
    this.l_misc = o.l_misc
    this.created_at = o.created_at
    this.updated_at = o.updated_at
    this.instance_id = o.instance_id
    this.SubLevelData = o.SubLevelData
    this.GameDataLog = o.GameDataLog

    this.unlock_after = o.unlock_after
    this.completion_image = o.completion_image
    this.clue_image = o.clue_image
    this.l_qr_id = o.l_qr_id
    this.scan_complete = o.scan_complete
    this.progress = o.progress
    this.is_locked = o.is_locked

    this.TextData = o.TextData
  }
}

export const leveldetailsStore = new Store<leveldetailsItem[] | false>(false)

export const leveldetailslstore = () => {
  const [leveldetails, setleveldetails] = useStore(leveldetailsStore)

  return {
    leveldetails,
    setleveldetails,

    async loadleveldetails() {
      await getleveldetails()
        .then(data => {
          console.log(data.data.results)
          setleveldetails(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    }
  }
}
