import { Store, useStore } from './Store'
import getlevel from '../utils/api/getlevel'
import postlevel from '../utils/api/postlevel'
import editlevel from '../utils/api/editlevel'
import deletelevel from '../utils/api/deletelevel'
import { logerror } from 'utils/actions'

export class levelItem {
  id: String
  l_level: String
  l_icon: String
  l_qrcode: String
  l_title: String
  l_stitle: String
  l_body: String
  l_bodytype: String
  l_bodyoptions: String
  locations: boolean
  l_media: String
  l_misc: String
  created_at: String
  updated_at: String
  instance_id: String
  SubLevelData: String
  GameDataLog: any
  unlock_after: String
  completion_image: String
  clue_image: String
  l_qr_id: String
  scan_complete: String
  progress: String
  is_locked: String
  TextData: any

  constructor(o: {
    id: String
    l_level: String
    l_icon: String
    l_qrcode: String
    l_title: String
    l_stitle: String
    l_body: String
    l_bodytype: String
    l_bodyoptions: String
    locations: boolean
    l_media: String
    l_misc: String
    created_at: String
    updated_at: String
    instance_id: String
    SubLevelData: any
    GameDataLog: any
    unlock_after: String
    completion_image: String
    clue_image: String
    l_qr_id: String
    scan_complete: String
    progress: String
    is_locked: String
    TextData: any
  }) {
    this.id = o.id
    this.l_level = o.l_level
    this.l_icon = o.l_icon
    this.l_qrcode = o.l_qrcode
    this.l_title = o.l_title
    this.l_stitle = o.l_stitle
    this.l_body = o.l_body
    this.l_bodytype = o.l_bodytype
    this.l_bodyoptions = o.l_bodyoptions
    this.locations = o.locations
    this.l_media = o.l_media
    this.l_misc = o.l_misc
    this.created_at = o.created_at
    this.updated_at = o.updated_at
    this.instance_id = o.instance_id
    this.SubLevelData = o.SubLevelData
    this.GameDataLog = o.GameDataLog

    this.unlock_after = o.unlock_after
    this.completion_image = o.completion_image
    this.clue_image = o.clue_image
    this.l_qr_id = o.l_qr_id
    this.scan_complete = o.scan_complete
    this.progress = o.progress
    this.is_locked = o.is_locked
    this.TextData = o.TextData
  }
}

export const levelStore = new Store<levelItem[] | false>(false)

export const levelstore = () => {
  const [level, setlevel] = useStore(levelStore)

  return {
    level,
    setlevel,

    async loadlevel() {
      await getlevel('')
        .then(data => {
          setlevel(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },

    async loadlevelwithid(id: any) {
      await getlevel(id)
        .then(data => {
          setlevel(data.data.results)
          console.log(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },

    async postlevel(data: any) {
      await postlevel(data)
        .then(res => {
          console.log(res)
          setlevel((oldArray: any) => [...oldArray, res.data])
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },

    async editlevel(id: any, data: any) {
      await editlevel(id, data)
        .then(res => {
          console.log(res)
          setlevel(oldarr => {
            return (
              oldarr &&
              oldarr.map((obj: any) => {
                const getobj = (o: any) => {
                  let x = o
                  x.l_level = data.l_level
                  x.instance_id = data.instance_id
                  x.l_icon = data.l_icon
                  x.l_qrcode = data.l_qrcode
                  x.l_title = data.l_title
                  x.l_stitle = data.l_stitle
                  x.l_body = data.l_body
                  x.l_bodytype = data.l_bodytype
                  x.l_bodyoptions = data.l_bodyoptions
                  x.locations = data.locations
                  x.l_media = data.l_media
                  x.l_misc = data.l_misc
                  x.completion_image = data.completion_image
                  x.clue_image = data.clue_image
                  x.l_qr_id = data.l_qr_id
                  x.unlock_after = data.unlock_after
                  return x
                }
                return String(obj.id) === String(id) ? getobj(obj) : obj
              })
            )
          })
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },

    async deletelevel(id: any) {
      await deletelevel(id)
        .then(res => {
          console.log(res)
          setlevel(level && level.filter(ele => ele.id !== id))
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    }
  }
}
