import { Store, useStore } from './Store'
import getsublevel_data_byid from '../utils/api/getsublevel_data_byid'
import { logerror } from 'utils/actions'

export class leveldetailsItem {
  created_at: String
  id: String
  instance_id: any
  level_id: any
  sl_body: String
  sl_bodyoptions: String
  sl_bodytype: String
  sl_icon: String
  sl_media: String
  sl_misc: null
  sl_stitle: String
  sl_title: String
  sl_types: String
  updated_at: String
  TextData: any

  constructor(o: {
    created_at: String
    id: String
    instance_id: any
    level_id: any
    sl_body: String
    sl_bodyoptions: String
    sl_bodytype: String
    sl_icon: String
    sl_media: String
    sl_misc: null
    sl_stitle: String
    sl_title: String
    sl_types: String
    updated_at: String
    TextData: any
  }) {
    this.id = o.id
    this.created_at = o.created_at
    this.instance_id = o.instance_id
    this.level_id = o.level_id
    this.sl_body = o.sl_body
    this.sl_bodyoptions = o.sl_bodyoptions
    this.sl_bodytype = o.sl_bodytype
    this.sl_icon = o.sl_icon
    this.sl_media = o.sl_media
    this.sl_misc = o.sl_misc
    this.sl_stitle = o.sl_stitle
    this.sl_title = o.sl_title
    this.sl_types = o.sl_types
    this.updated_at = o.updated_at
    this.TextData = o.TextData
  }
}

export const leveldetailsStore = new Store<leveldetailsItem | false>(false)

export const userActiveSubLevelDetailsStore = () => {
  const [ActiveSubLevelDetails, setActiveSubLevelDetails] = useStore(
    leveldetailsStore
  )

  return {
    ActiveSubLevelDetails,
    setActiveSubLevelDetails,

    async loadActiveSubleveldetails(id: any) {
      await getsublevel_data_byid(id)
        .then(data => {
          // console.log("sublevel", data.data.results[0]);
          setActiveSubLevelDetails(data.data.results[0])
        })
        .catch(err => {
          logerror(err, err.response)
        })
    }
  }
}
