import { Store, useStore } from "./Store";

export const Loading1 = new Store<String[]>([]);

export const useLoading = () => {

    const [Loading, setLoading] = useStore(Loading1);

    return {
        Loading, setLoading,
        pushLoading(newid: any) {
            setLoading((oldarr: any) => {
                oldarr.push(newid)
                return oldarr
            })
        },
        popLoading(remid: any) {
            setLoading((oldarr: any) => {
                let newarr = oldarr.filter((obj: any) => String(obj) !== String(remid))
                return newarr
            })
        },
        isLoading(id: any) {
            let state = false
            Loading.map((obj: any) => {
                if (String(obj) === String(id)) {
                    state = true
                }
            })
            return state
        }
    }
};

