import { Store, useStore } from './Store'
import getlogo from '../utils/api/getlogo'
import editlogo from '../utils/api/editlogo'
import postlogo from '../utils/api/postlogo'
import deletelogo from '../utils/api/deletelogo'
import { logerror } from 'utils/actions'
export class logoItem {
  id: String
  i_logo: String
  position: String
  section: String
  created_at: String
  updated_at: String
  instance_id: String

  constructor(o: {
    id: String
    i_logo: String
    position: String
    section: String
    created_at: String
    updated_at: String
    instance_id: String
  }) {
    this.id = o.id
    this.i_logo = o.i_logo
    this.position = o.position
    this.section = o.section
    this.created_at = o.created_at
    this.updated_at = o.updated_at
    this.instance_id = o.instance_id
  }
}

export const logoStore = new Store<logoItem[] | false>(false)

export const logostore = () => {
  const [logo, setlogo] = useStore(logoStore)

  return {
    logo,
    setlogo,

    async loadlogo() {
      await getlogo('')
        .then(data => {
          setlogo(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },
    async loadlogowithcallback(callback: any) {
      await getlogo('')
        .then(data => {
          callback(data.data.results)
          setlogo(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },
    async loadlogowithid(id: any) {
      await getlogo(id)
        .then(data => {
          setlogo(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },

    async postlogo(data: any) {
      await postlogo(data)
        .then(res => {
          setlogo((oldArray: any) => [...oldArray, res.data])
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },

    async editlogo(id: any, data: any) {
      await editlogo(id, data)
        .then(res => {
          setlogo(oldarr => {
            return (
              oldarr &&
              oldarr.map((obj: any) => {
                const getobj = (o: any) => {
                  let x = o
                  x.instance_id = data.instance_id
                  x.i_logo = data.i_logo
                  x.position = data.position
                  x.section = data.section
                  return x
                }
                return String(obj.id) === String(id) ? getobj(obj) : obj
              })
            )
          })
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },

    async deletelogo(id: any) {
      await deletelogo(id)
        .then(res => {
          setlogo(logo && logo.filter(ele => ele.id !== id))
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    }
  }
}
