import { Store, useStore } from './Store'
import getlogo_arrayposition from '../utils/api/getlogo_arrayposition'
import getlogo_arrayposition_iid from 'utils/api/getlogo_arrayposition_iid'
import { logerror } from 'utils/actions'

export class logopositionItem {
  id: String
  i_logo: String
  position: String
  section: String
  created_at: String
  updated_at: String
  instance_id: String

  constructor(o: {
    id: String
    i_logo: String
    position: String
    section: String
    created_at: String
    updated_at: String
    instance_id: String
  }) {
    this.id = o.id
    this.i_logo = o.i_logo
    this.position = o.position
    this.section = o.section
    this.created_at = o.created_at
    this.updated_at = o.updated_at
    this.instance_id = o.instance_id
  }
}

export class temp {
  section1: logopositionItem[][]
  section2: logopositionItem[][]
  section3: logopositionItem[][]
  constructor(o: {
    section1: logopositionItem[][]
    section2: logopositionItem[][]
    section3: logopositionItem[][]
  }) {
    this.section1 = o.section1
    this.section2 = o.section2
    this.section3 = o.section3
  }
}

export const logopositionStore = new Store<temp | false>(false)

export const logopositionstore = () => {
  const [logoposition, setlogoposition] = useStore(logopositionStore)

  return {
    logoposition,
    setlogoposition,

    async loadlogo_arrayposition() {
      await getlogo_arrayposition()
        .then(data => {
          setlogoposition(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },
    async loadlogo_arrayposition_iid(id: any) {
      await getlogo_arrayposition_iid(id)
        .then(data => {
          setlogoposition(data.data.results)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    }
  }
}
